import React from "react";
import Header from "./header.jsx";
import Footer from "./footer.jsx";
import Note from "./note.jsx";

function createNote(notes) {
  return <Note key={notes.key} title={notes.title} content={notes.content} />;
}

function App() {
  return (
    <div>
      <Header />
      {Note.map(createNote)}
      <Footer />
    </div>
  );
}

export default App;
